.why-us {
	&__container {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		margin-top: 5em;
		gap: 4em;
		place-items: center;
	}
	.section__box {
		justify-content: center;
		align-items: center;
		text-align: center;
		&-heading {
			.colour-span {
				color: #ffb800;
			}
		}
		
	}
	&__item {
		display: flex;
		gap: 4em;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
		padding: 3em 1em;
		&-text {
		}
		&-img {
			// display: flex;
			// align-items: center;
			background-color: #fffeea;
			padding: 3.5em;
			// padding: 4em;
			width: max-content;
			border-radius: 50%;
			& img {
				height: 60px;
				padding: 1em;
				width: auto;
			}
		}
		&-heading {
			color: #212121;
			font-weight: 600;
			font-size: clamp(1.8rem, 1.6895rem + 0.6316vw, 2.4rem);
		}
		&-description {
			color: #212121;
			font-size: clamp(1.6rem, 1.5816rem + 0.1053vw, 1.7rem);
			font-weight: 300;
		}
	}
	&__button {
		display: flex;
		align-items: center;
		gap: 0.5em;
		margin: 4em auto 0;
		background-color: #fff;
		color: #000000;
		// box-shadow: 0px 0px 56px -15px rgba(0, 0, 0, 0.15);
		border: 1px solid #ececec;
		font-weight: 500;
		font-size: clamp(1.6rem, 1.5263rem + 0.4211vw, 2rem);
		padding: 0.6em 1.6em;
		border-radius: 12px;
		width: max-content;
		& img {
			height: 30px;
			width: auto;
		}
	}
}
