.newsletter{
    &__box{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        text-align: center;
        gap: 3em;
        &-heading, &-text{
            color: #1A2024;
        }
        &-heading{
            line-height:1.5;
            font-size: clamp(2.8rem, 1.5765rem + 3.3987vw, 5.4rem);

        }
        &-text{
            font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.2rem);
        }
    }
    &__form{
        background-color: #FFB800;
        border-radius: 65px;
        display: flex;
        max-width: 600px;
        padding: 2em 2em;
        margin: 4vh auto;
        align-items: center;
        justify-content: space-between;
        &-input{
            width: 100%;
            border: none;
            font-size: clamp(1.6rem, 1.1294rem + 1.3072vw, 2.6rem);
            color: #fff;
            background: none;
            outline: none;
            padding-left: 1em;
            padding-right: .5em;
            font-weight: 300;
            &::placeholder{
                color: #eff3ff;
                font-weight: 200;
            }
        }
        &-submit{
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: 500;
            border: none;
            outline: none;
            font-size: clamp(1.6rem, 1.2235rem + 1.0458vw, 2.4rem);
            color: #1A2024;
            border-radius: 65px;
            background-color: #fff;
            padding: 0.6em 1.2em;
            cursor: pointer;
            & img{
                margin-left: .3em;
            }
        }
    }
}