@use "../base/colors" as *;
@use "../abstracts/mixins" as *;
@keyframes slideUp {
	to {
		opacity: 1;
		transform: translateY(0); /* End position */
	}
}
.portfolio {
	background-color: #131618;
	.section {
		&__box {
			text-align: left;
			color: #edf3ff;
		}
	}
	&__items {
		margin-top: 5em;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 2em;
		place-items: center;
		&-item {
			position: relative;
			width: 100%;
			height: auto;
			border-radius: 14px;
			overflow: hidden;
			padding-top: 100%;
			opacity: 0; /* Start hidden */
			transform: translateY(30px); /* Start from below */
			// &.show {
			// 	display: block;
			// 	position: relative;
			// 	opacity: 1;
			// 	width: 100%;
			// 	position: relative;
			// }

			& img {
				object-fit: cover;
				position: absolute;
				top: 0;
				left: 0;
				height: 100%;
				width: 100%;
			}
		}
	}
}

.show {
	animation: slideUp 0.5s forwards; /* Apply animation */
}
option {
	text-align: center;
}
.filter {
	&-bar {
		background-color: #1a2024;
		border-radius: 13px;
		width: max-content;
		// padding: 1.5em 3em;
		display: flex;
		margin: 3em 0;
	}
	&-btn {
		display: flex;
		position: relative;
		color: #fff;
		font-weight: 300;
		font-size: clamp(1.6rem, 1.4526rem + 0.8421vw, 2.4rem);
		outline: none;
		border: none;
		background: none;
		min-width: max-content;
		width: 100%;
		padding: 0.5em 2em;
		z-index: 1;
		border-radius: 13px;
		overflow: hidden;
		cursor: pointer;
		&::after {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: -1;
		}
		&.active {
			color: #ffb800;
			&::after {
				background-color: hsl(204, 16%, 14%);
			}
		}
	}
}
.load_more-button {
	display: block;
	font-size: clamp(1.6rem, 1.4526rem + 0.8421vw, 2.4rem);
	padding: 0.5em 2em;
	background-color: #1a2024;
	border-radius: 13px;
	border: none;
	outline: none;
	color: #fff;
	cursor: pointer;
	margin: 2em auto;
}
.filter-dropdown {
	display: none;
	margin: 1em 0;
	& select {
		border-radius: 13px;
		background-color: #1a2024;
		color: #fff;
		border: none;
		outline: none;
		appearance: none;
		text-align: center;
		font-size: 1.8rem;
		padding: 0.5em 1em;
	}
	option {
		outline: none;
		border: none;
	}
}
@media (max-width: 992px) {
	.filter-bar {
		display: none;
	}

	.filter-dropdown {
		display: block;
	}
}
