@use "../base/colors" as *;
@use "../abstracts/mixins" as *;

.iconCarousel {
	background-color: #131618;
	border-top: 1px solid #2b2b2b;
	padding: 4em 0;
    min-width: 100vw;
    width: max-content;
    margin: 0 auto;
	user-select: none;
	display: flex;
	gap: 20px;
    img{
        max-width: 100%;
    }
	& ul {
		list-style: none;
		flex-shrink: 0;
		min-width: 100%;
		display: flex;
		justify-content: space-evenly;
		align-items: center;
		gap: 20px;

		// animation: scroll 35s linear infinite;
        & li{
           
            & img{
                width: 100%;
            }
        }
		&:hover {
		}
	}
}
@keyframes scroll {
	to {
		transform: translateX(calc(-50% - 2rem));
	}
}


.iconCarousel {
	&-icon {
		height: 70px;
		width: auto;
	}
}
