@use "../base/colors" as *;
@use "../abstracts/mixins" as *;

.footer {
	border-top: 1px solid #ebebeb;
	padding-top: 6em;
	&__logo {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 2em;
		&-img {
			height: 100%;
			width: auto;
		}
		&-text {
			text-align: center;
			font-size: clamp(1.6rem, 1.5263rem + 0.4211vw, 2rem);
		}
	}
	&__box {
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		padding: 4em 0 10em;
		gap: 4em;
	}
	&__columns {
		display: flex;
		text-align: center;
		// justify-content: flex-end;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 4em;
	}
	&__column {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		&-heading {
			font-size: clamp(1.8rem, 1.5789rem + 1.2632vw, 3rem);
			color: #292a2a;
			font-weight: 600;
			text-transform: uppercase;
			margin-bottom: 0.5em;
		}
		&-text {
			display: block;
			font-weight: 300;
			font-size: clamp(1.6rem, 1.5632rem + 0.2105vw, 1.8rem);
			transition: color 0.3s;
			margin: 0.4em 0;
			position: relative;
			width: max-content;
			&::after {
				content: "";
				position: absolute;
				bottom: -3px;
				left: 0px;
				height: 2px;
				width: 100%;
				background-color: #ffb800;
				transform: scaleX(0);
				transform-origin: right;
				transition: transform 0.3s;
			}

			&:hover {
				&::after {
					transform: scaleX(1);
					transform-origin: left;
				}
			}
		}
	}
	&__policy {
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		text-align: center;
		font-size: clamp(1.6rem, 1.5632rem + 0.2105vw, 1.8rem);
		color: #2b2b2b;
		padding: 2em 0;
		border-top: 2px solid #2b2b2b;
		font-weight: 300;
	}
}
