.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-button {
  display: flex;
  color: #fff;
  font-weight: 300;
  font-size: clamp(1.6rem, 1.4526rem + 0.8421vw, 2.4rem);
  outline: none;
  border: none;
  background: none;
  min-width: max-content;
  width: 100%;
  padding: 0.5em 2em;
  z-index: 1;
  overflow: hidden;
  cursor: pointer;
}
.dropdown-button img {
  height: 35px;
  width: auto;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #131618;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  border-radius: 13px;
  z-index: 1;
  min-width: 160px;
}

.dropdown-content a {
  color: black;
  text-decoration: none;
  display: block;
  padding: 10px 15px;
  border-bottom: 1px solid #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}