@use "./base/colors" as *;
@use "./base/reset" as *;
@use "./layout/header" as *;
@use "./layout/navbar" as *;
@use "./abstracts/mixins" as *;
@use "./pages/services" as *;
@use "./pages/about" as *;
@use "./pages/iconCarousel" as *;
@use "./pages/faq" as *;
@use "./pages/portfolio" as *;
@use "./pages/contact" as *;
@use "./pages/newsletter" as *;
@use "./pages/whyUs" as *;
@use "./layout/footer" as *;

.no-scroll {
	overflow: hidden;
}
.section-padding {
	padding: 6vh 0;
}
.section__box {
	display: flex;
	justify-content: center;
	flex-direction: column;
	text-align: center;
	color: #131618;
	&-heading {
		font-size: clamp(2.8rem, 1.4667rem + 3.7037vw, 4.2rem);
	}
	&-text {
		font-size: clamp(1.6rem, 1.0667rem + 1.4815vw, 2.2rem);
	}
}
.container {
	padding: 0 4em;
}
.swiper {
	&-services {
		display: flex;
		flex-direction: column;
		position: relative;
		width: 100%;
		clip-path: inset(0 -200px 0 0);
		// overflow: visible !important;
		overflow: hidden !important;
		margin-left: auto;
		padding: 2em 2em 2em 5em !important;
		&::before,
		&::after {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			width: 20px; /* Szerokość gradientu */
			z-index: 10;
			pointer-events: none;
		}
		&::before {
			left: -2px;
			background: linear-gradient(
				to right,
				rgba(255, 255, 255, 1),
				rgba(255, 255, 255, 0)
			);
		}

		&-slide {
			border-radius: 50px;
			height: auto !important;
			transition: opacity 0.5s ease;
			// border: 1px solid #ebebeb;
			transition: transform 0.6s ease, opacity 0.5s ease;
			box-shadow: 0px 0px 56px -15px rgba(0, 0, 0, 0.15);
			&.active-slide {
				z-index: 1000;
			}
		}
		&-container {
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			overflow: visible;
			position: relative;
			max-width: 1500px;
			margin: 0 auto;
		}
	}
	// &::after {
	// 	width:20px;
	// 	right: 0;
	// 	background: linear-gradient(
	// 		to left,
	// 		rgba(255, 255, 255, 1),
	// 		rgba(255, 255, 255, 0)
	// 	);
	// }
	&-pagination {
		display: none;
		position: relative !important;
		justify-content: center;
		bottom: 0 !important;
		padding: 2em;
		align-items: center;
		&-bullet {
			transition: transform 0.1s;
			&-active {
				background-color: #ffb800 !important;
				transform: scale(1.5) !important;
			}
		}
	}

	&-button-prev,
	&-button-next {
		display: none !important;
		border-radius: 50%;
		position: relative !important;
		margin: 5em 0;
		left: 0 !important;
		top: 0 !important;
		box-shadow: 0px 4px 3.7px rgba(0, 0, 0, 0.09);
	}
	&-button-prev {
		background-color: #ffb800;
		color: #fff !important;
		padding: min(calc(3vh + 0.8vw), 44px) !important;

		&::after {
			content: "" !important;
		}
	}
	&-button-next {
		background-color: #fff;
		color: #ffb800 !important;
		padding: min(calc(2vh + 0.8vw), 30px) !important;
		&::after {
			content: "" !important;
		}
	}

	// &-wrapper{
	// 	width: 95%;
	// 	margin: 0 auto;
	// }
}
.swiper-container {
}
.swiper-pagination {
	display: flex;
}
.swiper-buittons {
	position: absolute;
	left: -20em;
	// left: -10em;
	top: 35%;
	transform: translate(0, -35%);
	display: none;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	z-index: 1000 !important;
	// margin-left: -28em;
}

.swiper-scrollbar {
	display: none;
}

@media (max-width: 992px) {
	.header {
		&__arrow {
			display: none;
		}
	}
}
@media (min-width: 320px) and (max-width: 768px) {
	.header {
		&__arrow {
			display: none;
		}
	}
	.swiper {
		&-services {
			padding: 5em 3em !important;
			&::after,
			&::before {
				width: 0;
			}
		}
	}
}
@media (min-width: 576px) {
	.swiper {
		&-services {
			padding: 10em 2em 10em 2em !important;
		}
	}
	.container {
		// padding: 0 5em !important;
	}
	.section {
		&-padding {
			padding: 6vh 0;
		}
		&__box {
			text-align: left;
		}
	}
	.service {
		&__box {
			padding: 4em 6em;
			&-image {
				padding: 4em;
				& img {
					height: 50px;
				}
			}
		}
	}
	.why-us {
		&__item {
			flex-direction: row;
			text-align: left;
		}
	}
}

@media (min-width: 768px) {
	.header {
		&__box {
			text-align: center;
			&-heading {
				& span {
					transform: rotate(-3deg);
				}
			}
		}
		&__buttons {
			flex-direction: row;
		}
	}
	.swiper {
		&-services {
			&-slide {
				transform: scale(0.92) !important;

				&.active-slide {
					transform: scale(1.05) !important;
					z-index: 1000;
				}
			}
		}
	}
	.about {
		&__buttons {
			flex-direction: row;
			justify-content: center;
			gap: 2em;

			&-button {
				width: max-content;
			}
		}
	}
}
@media (max-width: 991px) {
	body,
	html {
		scroll-padding-top: 40px;
	}
	.navbar {
		&__logo {
			display: none;
		}

		&__nav {
			position: relative;
			flex-direction: row;
			padding: 2em 0;
		}
		&__list {
			display: flex;
			position: absolute;
			flex-direction: column;
			justify-content: center;
			gap: 2em;
			width: 100vw;
			height: auto;
			padding: 9em 12em 5em 4em;
			top: 0;
			background-color: #1a2024;
			left: -4em;
			border-radius: 7px;
			transition: opacity 0.6s;
			pointer-events: none;
			opacity: 0;
			& li {
				& a {
					margin: 0;
				}
			}
			&.menu-active {
				display: flex;
				pointer-events: all;
				opacity: 1;
			}
		}
	}
	.burger {
		display: flex;
		z-index: 1000021312312 !important;
		position: relative;
	}
}
@media (min-width: 992px) {
	.navbar {
		display: flex;
		&__list {
			display: flex;
		}
	}
	.logo {
		max-height: 30px;
	}
	.header {
		background-image: url("../../dist/img/header_big.png");
	}
	.about {
		&__buttons {
			gap: 2em;
			justify-content: flex-start;
		}
		&__box {
			text-align: left;
			align-items: flex-start;
			&-heading {
				font-size: clamp(2.4rem, 0.4356rem + 3.1683vw, 4rem);
				color: #fff;
				font-weight: 600;
			}
		}
		&__row {
			flex-direction: row;
			&-img {
				max-height: none;
				padding-left: 10em;
			}
		}
	}
	.why-us {
		&__container {
			grid-template-columns: repeat(2, 1fr);
			margin-top: 15em;
		}
	}
	.portfolio {
		&__items {
			grid-template-columns: repeat(3, 1fr);
			gap: 6em;
		}
	}
	.footer {
		&__box {
			flex-direction: row;
		}
		&__logo {
			align-items: flex-start;
			justify-content: flex-start;
			&-text {
				text-align: left;
			}
		}
		&__column {
			align-items: flex-start;
		}
		&__columns {
			gap: 8em;
			text-align: left;
			flex-direction: row;
			align-items: flex-start;
		}
		&__policy {
			flex-direction: row;
		}
	}
}

@media (min-width: 1200px) {
	.service {
		&__box {
			justify-content: space-evenly;
		}
	}
	.swiper {
		&-services {
			padding: 10em 2em 10em 5em !important;

			&-container {
				padding: 0 13em 0 10em;
			}
		}
	}
	.container {
		padding: 0 15em;
	}
}

@media (min-width: 1400px) {
	.swiper-button-prev,
	.swiper-button-next {
		display: flex !important; /* Ukryj przyciski */
	}
	.swiper-buittons {
		display: flex;
	}
	.swiper-buittons {
		left: 5em;
	}
	// .our-services {
	// 	.container {
	// 		padding: 0 25em;
	// 	}
	// }
	.container {
		padding: 0 20em;
	}
	.swiper {
		&-pagination {
			display: none;
			margin-top: 5em;
		}
		&-services {
			&-container {
				padding: 0 20em;
			}
		}
	}
	.about {
		&__row {
			&-img {
				padding-left: 20em;
			}
		}
	}
}
@media (min-width: 1600px) {
	.swiper {
		&-pagination {
			display: none;
			margin-top: 5em;
		}
	}
}
@media (min-width: 1800px) {
	.container {
		padding: 0 10em;
	}
	.swiper {
		&-services {
			&-container {
				padding: 0 10em;
			}
		}
		&-buittons {
			left: -5em;
		}
	}
}
.dropdown {
	position: relative;
	display: inline-block;
}

.dropdown-button {
	display: flex;
	color: #fff;
	font-weight: 300;
	font-size: clamp(1.6rem, 1.4526rem + 0.8421vw, 2.4rem);
	outline: none;
	border: none;
	background: none;
	min-width: max-content;
	width: 100%;
	padding: 0.5em 2em;
	z-index: 1;
	overflow: hidden;
	cursor: pointer;
	& img {
		height: 35px;
		width: auto;
	}
}

.dropdown-content {
	display: none;
	position: absolute;
	background-color: #131618;
	box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
	border-radius: 13px;
	z-index: 1;
	min-width: 160px;
}

.dropdown-content a {
	color: black;
	text-decoration: none;
	display: block;
	padding: 10px 15px;
	border-bottom: 1px solid #ddd;
}

.dropdown-content a:hover {
}

.dropdown:hover .dropdown-content {
	display: block;
}
