@use "../base/colors" as *;
.navbar {
	width: 100%;
	position: fixed;
	border-bottom: 1px solid rgba(217, 217, 217, 0.15);
	z-index: 300000;
	align-items: center;
	justify-content: center;
    transition: background-color .3s, border-bottom .3s;
	&__link{
		color: #fff;
		transition: color .3s;
		&:hover{
			color: #ffb800;
		}
	}
    &-shadow{
        background-color: #131618;
        border-bottom: 1px solid #202020;
    }

}
.logo {
	max-height: 15px;
	// display: none;
	width: auto;
}
.navbar__nav {
	display: flex;
	flex-direction: row-reverse;
	align-items: center;
    justify-content: space-between;
	padding: 4em 0;
}
.navbar__list {
	display: none;

	width: 100%;
}
.navbar__list li {
	text-decoration: none;
	list-style-type: none;
}
.navbar__list li a {
	margin: 0.5em;
	text-decoration: none;
}
.navbar__item {
	& a {
		font-size: clamp(1.8rem, 1.6552rem + 0.8276vw, 2.2rem);
		font-weight: 200;
		color: #fff;
	}
}

.burger {
	display: none;
	flex-direction: column;
	background: transparent;
	border: none;
	outline: none;
	cursor: pointer;
	.line {
		margin: 0.3em;
		height: 0.2em;
		background-color: #fff;
		border-radius: 20px;
		transition: background-color 0.3s, width 0.3s;
		&:nth-child(1) {
			width: 1.4em;
		}
		&:nth-child(2) {
			width: 3em;
		}
		&:nth-child(3) {
			width: 3em;
		}
	}
}
.burger:hover .line {
	width: 3em;
}
